<template>
  <div class="container app-view">

    <div class="modal" :class="{active: onionOs.showModal}">
      <a class="modal-overlay"></a>
      <div class="modal-container">
        <div class="modal-header">
          <div class="modal-title h5">Update Complete</div>
        </div>
        <div class="modal-body">
          <div class="content label" style="padding: 10px">
            Your browser will refresh in a few seconds
          </div>
        </div>
      </div>
    </div>

    <ul class="tab tab-block">
      <li v-for="(tab, tabId) in tabs" class="tab-item" :key="tabId">
        <a class="c-hand"
          :class="{active: tabId === currentTab, badge: tab.badge}"
          @click="currentTab = tabId"
          :data-badge="tab.badge"
        >{{tab.name}}</a>
      </li>
    </ul>

    <div class="app-body">

      <ul class="menu app-menu">
        <li class="menu-item">
          <div class="tile tile-centered">
            <div class="tile-icon" @click="getApps"><img class="avatar" src="static/img/icons/store.svg"></div>
            <div class="tile-content">App Manager</div>
          </div>
        </li>
        <li class="divider"> </li>
        <li v-for="(tab, tabId) in tabs" class="menu-item" :key="tabId">
          <a class="c-hand"
            :class="{active: tabId === currentTab, badge: tab.badge}"
            @click="currentTab = tabId"
            :data-badge="tab.badge">
            {{tab.name}}
          </a>
        </li>
      </ul>

      <div class="app-content">
        <!-- App List -->
        <div class="card app-list">
          <div class="card-header">

            <!-- <div class="has-icon-right float-right" >
              <input type="text" class="form-input" placeholder="Search" v-model="appFilter">
              <i class="form-icon icon icon-cross  c-hand" v-if="appFilter !== ''" @click="appFilter = ''" ></i>
              <i class="form-icon icon icon-search" v-if="appFilter === ''" ></i>
            </div> -->

            <span class="card-title h5">Onion OS Apps </span>
            <!-- TODO: need a function to compare installed vs available version - right now a newer installed version than what's available shows up as out of date -->
            <div class="oos-upgrade-message float-right" v-show="!isLoading && onionOs.installed !== onionOs.available">
              A new version of OnionOS is available
              <button class="btn btn-success float-right"
              @click="updateOnionOs()"
              :class="{loading: isOOSUpdating, disabled: isInstalling}">Update Now</button>
            </div>

            <span class="loading-lg loading-icon float-right" :class="{loading: isLoading}"></span>

          </div>

          <div class="card-body app-list-body" v-show="!isLoading">
            <div class="card app-item" v-for="(app, idx) in availableApps" :key="app.id" v-show="appShow(app)">
              <div class="card-body" style="align-items:start">
                <img :src="`http://repo.onioniot.com/onionos/apps/icons/${app.id}.svg`" onerror="this.src = 'static/img/icons/onion.svg'" alt="" class="app-icon">

                <div class="app-description">
                  <strong>{{app.name}}</strong>
                  <div
                    class="tooltip float-right tooltip-left"
                    data-tooltip="This app made for the Omega2 Pro"
                    v-if="app.proOnly"
                    >
                    <span class="pro-badge badge" data-badge="Pro" ></span>
                  </div>
                  <br>
                  {{app.description | noTags}}<br>
                  <i class="app-version">Version {{displayAppVersion(app)}}</i>
                </div>
              </div>
              <div class="card-footer">
                <!-- <button class="btn btn-primary float-right" v-if="app.installed" @click="openApp(app)">Open</button> -->
                <button class="btn btn-error float-right" v-if="app.installed"
                @click="removeApp(app, idx)"
                :class="{loading: app.installing, disabled: isInstalling}">Remove</button>

                <!-- TODO: need a function to compare installed vs available version - right now a newer installed version than what's available shows up as out of date -->
                <button class="btn float-right" v-show="app.installed && (app.version !== app.installedVersion)"
                @click="updateApp(app, idx)"
                :class="{loading: app.installing, disabled: isInstalling}">Update</button>

                <button class="btn btn-success float-right" v-if="!app.installed && isCompatible(app)"
                @click="installApp(app, idx)"
                :class="{loading: app.installing, disabled: isInstalling}">Install</button>

                <span class="text-warning float-right" v-if="!isCompatible(app)">This app is not compatible with your device.</span>

              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  </div>

</template>

<script>
export default {
  name: 'AppManagerApp',
  data () {
    return {
      currentTab: 'available',
      tabs: {
        available: {name: 'Available'},
        installed: {name: 'Installed'}
      },
      isLoading: true,
      isInstalling: false,
      isOOSUpdating: false,
      installedApps: [],
      availableApps: [],
      onionOs: {
        id: 'onion-os',
        available: '',
        installed: '',
        showModal: false
      },
      appFilter: ''
    }
  },
  methods: {
    mergedAppList: function () {
      for (var j = 0; j < this.installedApps.length; j++) {
        var installedApp = this.installedApps[j]
        for (var i = 0; i < this.availableApps.length; i++) {
          var app = this.availableApps[i]
          if (installedApp.id === app.id) {
            app.installed = true
            app.installedVersion = installedApp.version
            this.$set(this.availableApps, i, app)
          }
        }
      }
    },
    appShow: function (app) {
      if (this.currentTab === 'installed') {
        return app.installed
      } else {
        return true
      }
    },
    displayAppVersion: function (app) {
      if (this.currentTab === 'installed') {
        return app.installedVersion
      } else {
        return app.version
      }
    },
    getApps: function () {
      return new Promise(function (resolve, reject) {
        this.isLoading = true
        this.availableApps = []
        // get the available Package list from the Onion repo
        this.$store.dispatch('httpGetRequest', {
          url: 'http://repo.onioniot.com.s3.amazonaws.com/omega2/packages/onion/Packages'
        })
          .then(resp => {
            // parse the data
            var packages = resp.data.match(/^Package: (\S+)/gm)
            var descriptions = resp.data.match(/^Description: (.+)/gm)
            var versions = resp.data.match(/^Version: (.+)/gm)
            for (var i = 0; i < packages.length; i++) {
              var pkg = packages[i].split(': ')
              if (pkg[1] && pkg[1].match(/^oos-/)) {
                // descriptions array holds
                //  "Description: <App Name> : <App Description>"
                var desc = descriptions[i].split(': ')

                this.availableApps.push({
                  id: pkg[1],
                  version: versions[i].split(': ')[1],
                  name: desc[1].slice(1, -1),
                  description: desc[2],
                  proOnly: desc[2].indexOf('[Omega2 Pro]') > -1
                })
              } else if (pkg[1] && pkg[1] === this.onionOs.id) {
                // check the available version of OnionOS
                this.onionOs.available = versions[i].split(': ')[1]
              }
            }
            this.mergedAppList()

            resolve()
          })
      }.bind(this))
    },
    getInstalledApps: function () {
      return new Promise(function (resolve, reject) {
        this.$store.dispatch('handleCommand', {cmd: 'opkg', params: ['list-installed']}).then(resp => {
          this.installedApps = []
          for (var pkg of resp.stdout.split('\n')) {
            if (pkg.indexOf('oos-app') > -1) {
              var pkgData = pkg.split(' - ')
              this.installedApps.push({
                id: pkgData[0],
                version: pkgData[1]
              })
            } else if (pkg.match(/^onion-os /)) {
              // find the installed version of OnionOS
              var data = pkg.split(' - ')
              this.onionOs.installed = data[1]
              // TEMP: hardcoded to make OnionOS always update
              // TODO: have a global debug mode switch
              // this.onionOs.installed = '0.0.1'
            }
          }
          this.mergedAppList()
          resolve()
        })
      }.bind(this))
    },
    openApp: function (app) {
      this.$store.dispatch('launchApp', {appId: app.id})
    },
    opkgOperation: function (opkgCmd) {
      return new Promise(function (resolve, reject) {
        this.isInstalling = true

        this.$store.dispatch('handleScript', {code: opkgCmd}).then(resp => {
          return this.$store.dispatch('waitProcess', {pid: resp.pid})
        }).then(() => {
          this.isInstalling = false
          this.$store.dispatch('refreshAppList')
          resolve()
        })
      }.bind(this))
    },
    installApp: function (app, idx) {
      // TODO: convert this to use opkgOperation function instead
      this.isInstalling = true
      app.installing = true
      this.$set(this.availableApps, idx, app)

      this.$store.dispatch('handleScript', {code: `opkg update; opkg install ${app.id}`}).then(resp => {
        return this.$store.dispatch('waitProcess', {pid: resp.pid})
      }).then(() => {
        app.installing = false
        app.installed = true // TODO: notice: not included in opkgOperation function
        app.installedVersion = app.version
        this.isInstalling = false
        this.$set(this.availableApps, idx, app) // TODO: notice: not included in opkgOperation function
        this.$store.dispatch('refreshAppList')
      })
    },
    removeApp: function (app, idx) {
      // TODO: convert this to use opkgOperation function instead
      this.isInstalling = true
      app.installing = true
      this.$set(this.availableApps, idx, app)

      this.$store.dispatch('handleScript', {code: `opkg remove ${app.id}`}).then(resp => {
        return this.$store.dispatch('waitProcess', {pid: resp.pid})
      }).then(() => {
        app.installing = false
        app.installed = false // TODO: notice: not included in opkgOperation function
        app.installedVersion = null // TODO: notice: not included in opkgOperation function
        this.isInstalling = false
        this.$set(this.availableApps, idx, app) // TODO: notice: not included in opkgOperation function
        this.$store.dispatch('refreshAppList')
      })
    },
    updateApp: function (app, idx) {
      app.installedVersion = app.version
      app.installing = true
      this.$set(this.availableApps, idx, app)

      this.opkgOperation(`opkg update; opkg upgrade ${app.id}`)
        .then(() => {
          app.installing = false
          app.installed = true
          this.$set(this.availableApps, idx, app)
        })
    },
    updateOnionOs: function () {
      this.isOOSUpdating = true
      this.opkgOperation(`opkg update; opkg upgrade ${this.onionOs.id}`)
        .then(() => {
          this.isOOSUpdating = false
          this.onionOs.showModal = true
          setTimeout(function () {
            location.reload()
          }, 3000)
        })
    },
    isCompatible: function (app) {
      if (app.proOnly) {
        return this.$store.state.device.board_name === 'omega2pro'
      }
      return true
    }
  },
  created: function () {
    var p1 = this.getApps()
    var p2 = this.getInstalledApps()

    Promise.all([p1, p2])
      .then(() => {
        this.isLoading = false
      })
  },
  filters: {
    noTags: function (value) {
      return value.replace('[Omega2 Pro]', '')
    }
  }
}
</script>

<style scoped>
.app-version {
  color: #7f8c8d;
}

.btn {
    margin-left: 10px;
}
.avatar {
  background: inherit;
}

.app-content {
  max-width: 800px;
  width: 100%;
}

.app-list {
  text-align: left;
  width: 100vw;
  max-width: 800px;
  margin: auto;
  /* margin-top: 20px; */
}
.app-view {
  padding: 20px;
  background-color: #f8f9fa;
  height: 100%;
  overflow: auto;
}
.app-menu {
  /* margin: 16px; */
  margin-right: 40px;
  z-index: 10;
}
.app-body {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.app-list-body {
  display: flex;
  /* flex-direction: */
  flex-wrap: wrap;
  align-content: space-evenly;
  justify-content: space-between;
}

.app-item {
  width: 100%;
  max-width: 360px;
  margin: 5px;
}

.tab {
  display: none;
}

.loading-icon {
  margin-right: 30px;
}

.card-body {
  display: flex;
  align-items: center;
}

.app-icon {
  width: 80px;
  margin-right: 10px;
}

.oos-upgrade-message {
  display: flex;
  flex-direction: row;
  align-items: center;
  background-color: #fff6e5;
  padding: 5px 10px 5px 10px;
  border-radius: 5px;
  border: solid 1px #ffc04c;

}

.pro-badge::after {
  background: #f39c12;
}

@media only screen and (max-width: 1050px)  {
  .tab {
    display: flex;
  }
  .app-menu {
    display: none;
  }
  .app-view {
    padding: 0;
  }
}

@media only screen and (max-width: 774px)  {
  .app-list-body {
    justify-content: center;
  }
}
</style>



